/*===== LOOP =====*/
.acpt-loop.grid-1, .acpt-loop.grid-2, .acpt-loop.grid-3, .acpt-loop.grid-4, .acpt-loop.grid-5, .acpt-loop.grid-6 {
  display: grid;
