.acpt-loop.grid-1 {
  grid-template-columns: 1fr; }

.acpt-loop.grid-2 {
  grid-template-columns: 1fr 1fr; }

.acpt-loop.grid-3 {
  grid-template-columns: 1fr 1fr 1fr; }

.acpt-loop.grid-4 {
  grid-template-columns: 1fr 1fr 1fr 1fr; }

.acpt-loop.grid-5 {
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr; }

.acpt-loop.grid-6 {
