/*========== GRID SYSTEM ==========*/
.acpt-grid {
  display: grid;
  grid-auto-rows: 1fr;
  gap: 20px;
  margin-bottom: 20px; }
  .acpt-grid.col-6 {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr; }
  .acpt-grid.col-4 {
    grid-template-columns: 1fr 1fr 1fr 1fr; }
  .acpt-grid.col-3 {
    grid-template-columns: 1fr 1fr 1fr; }
  .acpt-grid.col-2 {
    grid-template-columns: 1fr 1fr; }
  .acpt-grid.col-1 {
    grid-template-columns: 1fr; }
  @media (max-width: 767px) {
    .acpt-grid.col-6, .acpt-grid.col-4, .acpt-grid.col-3, .acpt-grid.col-2, .acpt-grid.col-1 {
      grid-template-columns: 1fr; } }
  .acpt-grid .element {
    height: 100%; }

