/*========== TABLES ==========*/
.acpt-table-responsive {
  overflow-x: auto; }

.acpt-table {
  border-spacing: 0;
  border-collapse: separate;
  width: 100%;
  font-size: 14px; }
  .acpt-table tr {
    padding: 0;
    margin: 0; }
  .acpt-table th,
  .acpt-table td {
    padding: calc(20px / 1.3) 20px; }
  .acpt-table th {
    text-align: left;
    color: #fff;
    background: #003367;
    border-bottom: 2px solid #003367; }
  .acpt-table th.grey {
    color: #003367;
    background: #e7f3ff;
    border-bottom: 2px solid #e7f3ff; }
  .acpt-table th.backend {
    width: 65%; }
  .acpt-table th.frontend {
    width: 35%; }
  .acpt-table th .helper {
    color: #fff;
    cursor: help;
    position: relative;
    top: 3px; }
  .acpt-table th .helper svg {
    fill: #fff;
    width: 18px;
    height: 18px; }
  .acpt-table .with-border {
    border-right: 3px solid rgba(0, 0, 0, 0.05); }
  .acpt-table.table-secondary th {
    color: #444;
    background: #e7f3ff;
    border-bottom: 2px solid rgba(0, 0, 0, 0.05); }
  .acpt-table td {
    text-align: left;
    color: #444;
    border-bottom: 2px solid #f1f1f1; }
  .acpt-table td.backend .hover-links {
    visibility: hidden; }
  .acpt-table td.backend:hover .hover-links {
    visibility: visible; }
  .acpt-table td.frontend {
    text-align: center; }
  .acpt-table td.no-wrap {
    white-space: nowrap; }
  .acpt-table td.fixed {
    position: fixed; }
  .acpt-table tr:nth-child(even) td {
    background: #f1f8ff; }
  .acpt-table tr:nth-child(even):hover td,
  .acpt-table tr:hover td {
    color: #003367;
    background: #e7f3ff; }

